    if (response.err || !response.shellIsOk || !response.dataIsOk) {
      const errorMessage = response.err?.message || 'Failed to fetch available meetings';
      throw new Error(errorMessage);
    }

    if(!response.data?.data) {

    }
    // Ê£ÄÊü•‰∏öÂä°ÈÄªËæëÁä∂ÊÄÅ
