import React, { useState, useEffect } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  Button,
  Avatar,
  Chip,
  IconButton,
  List,
  ListItem,
  Alert,
  CircularProgress,
  Divider,
  Stack
} from '@mui/material';
import {
  Refresh as RefreshIcon,
  VideocamOutlined as VideoIcon,
  Person as PersonIcon,
  AccessTime as TimeIcon
} from '@mui/icons-material';
import {
  AvailableMeeting,
  getAvailableMeetings,
  buildMeetingUrl,
  formatMeetingTime,
  getRoleDisplayText,
  getMeetingStatusInfo,
  getEventTypeInfo
} from '../../services/meetingService';
import { useProxyWatch } from '@fanfanlo';
import { user } from '@app/model';
import { autoWebViewJs } from '@droid/android';

interface MeetingListProps {
  className?: string;
  onMeetingJoin?: (meeting: AvailableMeeting) => void;
}

const MeetingList: React.FC<MeetingListProps> = ({ className, onMeetingJoin }) => {
  const [meetings, setMeetings] = useState<AvailableMeeting[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const [token] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);

  // Check if running in Android WebView environment
  const isAndroidWebView = (): boolean => {
    try {
      // Check if autoWebViewJs is available
      const hasAutoWebViewJs = !!(autoWebViewJs && typeof autoWebViewJs.callScript === 'function');
      return hasAutoWebViewJs;
    } catch (e) {
      console.warn('üîç MeetingList: Error checking Android WebView environment:', e);
      return false;
    }
  };
  // Âä†ËΩΩ‰ºöËÆÆÂàóË°®
  const loadMeetings = async () => {
    setLoading(true);
    setError(null);
    try {
      const meetingList = await getAvailableMeetings();
      setMeetings(meetingList);
      console.log('üì∫ Loaded meetings:', meetingList);
    } catch (err) {
      const errorMsg = err instanceof Error ? err.message : 'Âä†ËΩΩ‰ºöËÆÆÂàóË°®Â§±Ë¥•';
      setError(errorMsg);
      console.error('üì∫ Failed to load meetings:', err);
    } finally {
      setLoading(false);
    }
  };

  // ÂàùÂßãÂä†ËΩΩ
  useEffect(() => {
    if(!token)return;
    loadMeetings();
  }, [token]);

  // Âä†ÂÖ•‰ºöËÆÆ
  const handleJoinMeeting = (meeting: AvailableMeeting) => {
    console.log('üì∫ Joining meeting:', meeting);
    
    // Check if this is a security call and we're in Android environment
    const isSecurityCall = meeting.access_mode === 'security' || meeting.short_code.startsWith('security-');
    const inAndroidWebView = isAndroidWebView();
    
    if (isSecurityCall && inAndroidWebView) {
      // For security calls in Android environment, launch native activity
      try {
        console.log('üîí Launching Android security call activity');
        const script = `com.fanfanlo.emergencycall.manager.SecurityCallManager.startSecurityVideoCall();`;
        autoWebViewJs.callScript(script);
        return;
      } catch (error) {
        console.error('üîí Failed to launch Android security call:', error);
        // Fall back to web meeting if Android call fails
      }
    }
    
    if (onMeetingJoin) {
      onMeetingJoin(meeting);
    } else {
      // ÈªòËÆ§Ë°å‰∏∫ÔºöË∑≥ËΩ¨Âà∞‰ºöËÆÆÈ°µÈù¢
      const meetingUrl = buildMeetingUrl(meeting.short_code, meeting.role, meeting.event_type);
      window.location.href = meetingUrl;
    }
  };

  // Ê∏≤ÊüìÂçï‰∏™‰ºöËÆÆÈ°π
  const renderMeetingItem = (meeting: AvailableMeeting) => {
    const statusInfo = getMeetingStatusInfo(meeting.status);
    const roleText = getRoleDisplayText(meeting.role);
    const eventTypeInfo = getEventTypeInfo(meeting.event_type || 'security');
    const timeText = formatMeetingTime(meeting.created_at);
    return (
      <Card 
        key={meeting.event_uuid} 
        sx={{ 
          mb: 2, 
          cursor: 'pointer',
          transition: 'all 0.2s ease',
          '&:hover': {
            boxShadow: 3,
            transform: 'translateY(-2px)'
          }
        }}
        onClick={() => handleJoinMeeting(meeting)}
      >
        <CardContent>
          <Box sx={{ display: 'flex', alignItems: 'flex-start', gap: 2 }}>
            {/* ÂèëËµ∑ËÄÖÂ§¥ÂÉè */}
            <Avatar 
              src={meeting.initiator_info.avatar_url} 
              sx={{ width: 48, height: 48 }}
            >
              {meeting.initiator_info.display_name.charAt(0)}
            </Avatar>

            {/* ‰ºöËÆÆ‰ø°ÊÅØ */}
            <Box sx={{ flex: 1 }}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
                <Typography variant="h6" component="div" sx={{ fontWeight: 600 }}>
                  {meeting.initiator_info.display_name}
                </Typography>
                <Chip 
                  label={`${eventTypeInfo.icon} ${eventTypeInfo.text}`}
                  size="small"
                  sx={{ 
                    fontSize: '0.75rem',
                    backgroundColor: eventTypeInfo.color,
                    color: 'white'
                  }}
                />
                <Chip 
                  label={roleText}
                  size="small"
                  color={meeting.role === 'initiator' ? 'primary' : 'secondary'}
                  sx={{ fontSize: '0.75rem' }}
                />
                <Chip 
                  label={statusInfo.text}
                  size="small"
                  sx={{ 
                    fontSize: '0.75rem',
                    backgroundColor: statusInfo.color,
                    color: 'white'
                  }}
                />
              </Box>

              <Stack direction="row" spacing={2} sx={{ mb: 1 }}>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                  <VideoIcon sx={{ fontSize: 16, color: 'text.secondary' }} />
                  <Typography variant="body2" color="text.secondary">
                    {meeting.short_code}
                  </Typography>
                </Box>
                <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5 }}>
                  <TimeIcon sx={{ fontSize: 16, color: 'text.secondary' }} />
                  <Typography variant="body2" color="text.secondary">
                    {timeText}
                  </Typography>
                </Box>
              </Stack>

              {meeting.access_mode && (
                <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>
                  ËÆøÈóÆÊ®°Âºè: {meeting.access_mode}
                </Typography>
              )}
            </Box>

            {/* Âä†ÂÖ•ÊåâÈíÆ */}
            <Button
              variant="contained"
              size="small"
              startIcon={<VideoIcon />}
              sx={{ minWidth: 100 }}
              onClick={(e) => {
                e.stopPropagation();
                handleJoinMeeting(meeting);
              }}
            >
              Âä†ÂÖ•‰ºöËÆÆ
            </Button>
          </Box>
        </CardContent>
      </Card>
    );
  };

 if(!token){
  return <></>
 }
  return (
    <Box className={className} sx={{ width: '100%', maxWidth: 800, mx: 'auto', p: 2 }}>
      {/* Ê†áÈ¢òÂíåÂà∑Êñ∞ÊåâÈíÆ */}
      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between', mb: 3 }}>
        <Typography variant="h5" component="h1" sx={{ fontWeight: 600 }}>
          ÂèØÂèÇ‰∏éÁöÑÈü≥ËßÜÈ¢ë‰ºöËÆÆ
        </Typography>
        <IconButton 
          onClick={loadMeetings} 
          disabled={loading}
          sx={{ 
            backgroundColor: 'primary.main',
            color: 'white',
            '&:hover': { backgroundColor: 'primary.dark' }
          }}
        >
          <RefreshIcon />
        </IconButton>
      </Box>

      {/* ÈîôËØØ‰ø°ÊÅØ */}
      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
          <Button 
            size="small" 
            onClick={loadMeetings} 
            sx={{ ml: 1 }}
          >
            ÈáçËØï
          </Button>
        </Alert>
      )}

      {/* Âä†ËΩΩÁä∂ÊÄÅ */}
      {loading && (
        <Box sx={{ display: 'flex', justifyContent: 'center', py: 4 }}>
          <CircularProgress />
          <Typography variant="body2" sx={{ ml: 2 }}>
            Âä†ËΩΩ‰ºöËÆÆÂàóË°®‰∏≠...
          </Typography>
        </Box>
      )}

      {/* ‰ºöËÆÆÂàóË°® */}
      {!loading && !error && (
        <>
          {meetings.length === 0 ? (
            <Box sx={{ textAlign: 'center', py: 6 }}>
              {/* <VideoIcon sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} /> */}
              {/* <Typography variant="h6" color="text.secondary" gutterBottom>
                ÊöÇÊó†ÂèØÂèÇ‰∏éÁöÑ‰ºöËÆÆ
              </Typography> */}
              {/* <Typography variant="body2" color="text.secondary">
                ÂèëËµ∑Êñ∞ÁöÑÁ¥ßÊÄ•ÂëºÊïëÊàñÁ≠âÂæÖ‰ªñ‰∫∫ÈÇÄËØ∑ÊÇ®ÂèÇ‰∏é‰ºöËÆÆ
              </Typography> */}
            </Box>
          ) : (
            <Box>
              <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
                ÂÖ±ÊâæÂà∞ {meetings.length} ‰∏™ÂèØÂèÇ‰∏éÁöÑ‰ºöËÆÆ
              </Typography>
              {meetings.map(renderMeetingItem)}
            </Box>
          )}
        </>
      )}
    </Box>
  );
};

export default MeetingList;